import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './plugins/base'
import './plugins/chartist'
import './plugins/vee-validate'
import './plugins/vue-world-map'
import vuetify from './plugins/vuetify'
import i18n from './i18n'
import ego from './components/Utlis/http'
import Print from 'vue-print-nb'
import GlobalSettings from './components/Settings/GlobalSettings'
import { findBestIp } from './components/Utlis/api'
import { IKUtils } from 'innerken-utils'

console.log(GlobalSettings.DefaultLocale)
const DeviceId = IKUtils.getQueryString('DeviceId') ? IKUtils.getQueryString('DeviceId') : -1
if (parseInt(DeviceId) !== -1) {
  findBestIp(DeviceId).then(
    res => {
      store.commit('SAVE_CURRENT_DEVICE', {
        deviceId: DeviceId,
        url: res.content,
      })
      init()
    },
  )
} else {
  store.commit('SAVE_CURRENT_DEVICE', {
    deviceId: DeviceId,
    url: 'localhost',
  })
  init()
}

function init () {
  Vue.config.productionTip = true
  ego.initial(store.getters.rootUrl + GlobalSettings.endPoint)
  Vue.use(Print)
  new Vue({
    router,
    store,
    vuetify,
    i18n,
    render: h => h(App),
  }).$mount('#app')
}
